/**
 * App
 */
.app {
  /* text-align: center; */
}

/**
 * Banner
 */
.banner {
  padding: 50px 10%;
  min-width: 360px;
  min-height: 200px;
  text-align: center;
}

@media only screen and (min-width: 768px) {
  .banner {
    padding: 50px 20%;
  }
}
  
.banner-content {
  max-width: 1500px;
  margin: auto;
}

.banner-content>h2 {
  margin: 0 0 30px;
}

.banner-content>p {
  color: #474747;
}

/**
 * Hero
 */
.hero {
  padding: 0 10%;
  height: 500px;
  background: #50bebe; /*  #6dc9c9; dcedc1, a8e6cf, c3e2df, #043953, 60c6cd, cedcf2, 687892 */ /* cv light/dark: #31849b/215868 */
  z-index: 1000;
  overflow: hidden;
}

@media only screen and (min-height: 768px) {
  .hero {
    position: sticky;
  }
}

.hero>.banner-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 1800px;
}

.hero-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 100%;
}

.conversation {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-grow: 1;
  height: 100%;
  position: relative;
}

.speech-bubble {
  background: #fbfeff;
  padding: 0 10px;
  height: 70px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
  /* box-shadow: 8px 14px 38px rgba(45, 51, 56, 0.08), 1px 3px 8px rgba(45, 51, 56, 0.03); */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.speech-bubble p {
  margin: 0;
}

.hero .greeting {
  position: absolute;
  margin-left: 10px;
}

.hero .greeting:after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-right-color: #fff;
	border-left: 0;
	border-top: 0;
	margin-top: 0;
	margin-left: -12px;
}

.hero .action {
  width: 60%;
  right: 0;
  position: absolute;
}

.hero .action:after {
	content: '';
	position: absolute;
  right: 0;
  bottom: 0;
	width: 0;
	height: 0;
	border: 30px solid transparent;
	border-left-color: #fff;
	border-right: 0;
	border-bottom: 0;
	margin-top: 0;
	margin-right: -12px;
}

.hero img {
  width: 400px;
  height: 400px;
  border-radius: 100%;
  border: 3px solid #b3ddd9;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
}

/**
 * Paragraph
 */
.paragraph h2 {
  text-align: left;
}

.paragraph p {
  text-align: justify;
}

.paragraph-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/**
 * Showcase
 */
.showcase {
  background: #f2f6f9;
  padding: 50px 20px 80px;
  /* padding-bottom: 80px; */
}

.showcase h2 {
  /* margin: 30px 0 40px; */
}

.showpieces a {
  color: rgb(43, 43, 43);
  text-decoration: none;
}

.showpiece p {
  color: #474747;
}

.showpieces {
  /* padding: 0; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.showpiece {
  position: relative;
  top: 0;
  margin: 20px;
  width: 300px;
  height: 400px;
  padding: 10px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 8px 14px 38px rgba(45, 51, 56, 0.08), 1px 3px 8px rgba(45, 51, 56, 0.03);
  transition: 0.5s ease;
}

.showpiece:hover {
  top: -10px;
}

.showpiece img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

@media only screen and (min-width: 1100px) {
  .showpieces {
    /* flex-direction: row;
    flex-wrap: wrap; */
    /* justify-content: center; */
  }
}

/**
 * Footer
 */
.footer {
  /* padding: 80px 20px; */
  /* padding-top: 80px; */
  padding-left: 5%;
  padding-right: 5%;
  min-height: 0;
  padding-top: 80px;
  padding-bottom: 80px;
}

/**
 * Misc
 */
.social-button {
  display: inline-block;
  margin: 10px;
  font-size: 1rem;
  padding: 3px 10px;
  border-radius: 5px;
  border: none;
  background: #b3ddd9;
  text-decoration: none;
  color: #474747;
}

.social-button i {
  margin-right: 6px;
}
